export class DataSetModel
{
    data: Array<number>;
    borderColor: string;
    borderWidth: number;

    constructor(input : any)
    {
        this.data= input.data ? input.data : [];
        this.borderColor = input.borderColor ? input.borderColor : '#66ccff';
        this.borderWidth = input.borderWidth ? input.borderWidth : '1';
    }
}